
.loadingContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    background: transparent;
    margin: 0;
}

.loading {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 400px;
    height: 50px;
    background: white;
    border-radius: 15px;
    padding: 2px 10px;
    margin: 8px 0 8px 0 ;
}


.complet {
    margin-right: 10px;
    width: 40px;
    height: 40px;
    border-radius: 10px;
    background-color: black;
    padding: 5px 5px 5px 5px;
}

.delete {
    margin-left: 10px;
    width: 40px;
    height: 40px;
    border-radius: 10px;
    background-color: black;
    padding: 5px 5px 5px 5px;
}

.loading,
.complet, 
.delete {
    background: linear-gradient(90deg,rgba(250,250,250,1),rgb(200,199,199));
    background-size: 400% 400%;
    animation: loadingAnimation 3s ease-in-out infinite;
}

@keyframes loadingAnimation {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}