
form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: linear-gradient(rgba(160, 193, 255, 1),rgba(188, 158, 252, 1),rgba(53, 1, 164, 1))/* rgb(211, 211, 211) */;
    
    width: 90%;
    padding: 30px;
    gap: 8px;
    max-width: 400px;
    box-sizing: border-box;
    border-radius: 8px;
}

label {
    font-weight: bold;
    font-size: 18px;
    color: black;
}

textarea {
    width: 250px;
    height: 100px;
    font-size: 14px;
    font-weight: normal;
    background-color: rgba(235, 233, 233, 0.301);
    box-sizing: border-box;
    padding: 6px;
    border: 1px solid ;
    border-radius: 4px;
}
textarea:hover {
    background-color: rgb(213 210 210 / 49%)/* rgb(255, 255, 255) */;
    border: 1px solid rgba(0, 172, 240, 0.425);
}
textarea:focus {
    background-color: rgb(255, 255, 255);
    border: 2px solid rgba(0, 172, 240, 0.712);
}

.formContainerButton {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 50px;
    
}

.btnForm {
    border-radius: 2px;
    width: 100px;
    height: 40px;
    border: none;
    font-weight: bold;
    cursor: pointer;
}
.btnCancelar {
    background: rgb(221 55 55);

}

.btnCancelar:hover {
    background: rgba(231, 33, 33, 0.938);

}

.btnGuardar {
    background-color: rgb(91 214 231 / 79%);

}

.btnGuardar:hover {
    background-color: rgba(67, 221, 241, 0.897);

}


.closeModal {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bolder;
    color: white;
    width: 30px;
    height: 30px;
    background: rgba(255, 0, 0, 0.514);
    border-radius: 50%;
    position: absolute;
    top: 183px;
    right: 452px;
}

.closeModal:hover {
    background: rgba(160, 3, 3, 0.842);
    cursor: pointer;
}
